import Communicator from '../common/Communicator.js';
import Constants from '../common/Constants.js';
import CommunicatorEvent from '../common/event/CommunicatorEvent.js';
export default class ClothImpostor
{
    constructor(_objInit)
    {
        //super();
        if (_objInit !== ClothImpostor.m_objInit) 
        {
            throw new Error( "ClothImpostor     - ClothImpostor.GetInstance()" ); 
           // return;
        }
    }

    static GetInstance(_scene)
    {
        if(ClothImpostor.m_clothImpostor == null) 
        {
            ClothImpostor.m_clothImpostor = new ClothImpostor(ClothImpostor.m_objInit);
            ClothImpostor.m_objInit = {};

            ClothImpostor.LOADED_DATA = 0;
        }
        return ClothImpostor.m_clothImpostor;
    }

    
    // static 
};

