let ContainerBox = styled.div`
    position: relative;
    margin: 0px;
    padding: 0px;
`;

let Block = styled(ContainerBox)`
    display: ${(props) => ((props.Display !== null) ? props.Display : null )};
    width: ${props => props.ContainerWidth};
    height: ${props => ((props.ContainerHeight))};
    background-color: ${(props) => ((props.BackColor !== null) ? props.BackColor : null )};
    border: ${(props) => (props.Border !== null ? props.Border + " solid #000" : "0px solid #000" )};
`;

let TextBox = styled.div`
    color: ${props=>props.FontColor}; font-size: ${props=>props.FontSize}; font-weight: ${props=>props.FontWeight};
    font-family: 'SEBANG_Gothic_Regular';
    word-spacing: ${(props) => ((props.WordSpacing !== null) ? props.WordSpacing : null )};
    letter-spacing: ${(props) => ((props.LetterSpacing !== null) ? props.LetterSpacing : null )};
    text-align: ${(props) => ((props.TextAlign !== null) ? props.TextAlign : null )};
    margin-top: ${props=>props.MarginTop};
    margin-bottom: ${props=>props.MarginBottom};
    pointer-events:none;
    border: ${(props) => (props.Border !== null ? props.Border + " solid #000" : "0px solid #000" )};
`;










