import * as BABYLON from "babylonjs";
import gsap from "gsap";
import Communicator from "../common/Communicator";
import Constants from "../common/Constants";
import DataManager from "../common/data/DataManager";
import CommunicatorEvent from "../common/event/CommunicatorEvent";
import AnimationManager from "../content/AnimationManager";
import ContentsManager from "./ContentsManager";

export default class CameraManager {
  static PLAY_MODE = "Play_Mode";
  static MINI_MAP_MODE = "Mini_Map_Mode";
  static BOOTH_MODE = "Booth_Mode";

  static meshLookAt;
  static meshTarget;
  static currentMode;
  static oldMode;
  static locator;
  static aniFlow;

  static isPedstalLanding = false;

  /**
   *===============================================================================
   * constructor()      :
   * @param _objInit   :    
   *===============================================================================*/
  constructor(_objInit) {
    //super();

    if (_objInit !== CameraManager.m_objInit) {
      throw new Error("CameraManager     - CameraManager.GetInstance()");
      // return;
    }
  }

  /**
   *===============================================================================
   * dispose()               :
   *===============================================================================*/
  static dispose() {
    gsap.killTweensOf(CameraManager.meshLookAt);
    gsap.killTweensOf(CameraManager.camera);

    CameraManager.m_iSwingCountFrame = 0;

    CameraManager.meshTarget = null;
    CameraManager.camera.dispose();
    CameraManager.m_cameraManager = null;
    CameraManager.m_objInit = null;
  }

  /**
   *===============================================================================
   * GetInstance()      :
   * @return            :  
   *===============================================================================*/
  static GetInstance() {
    if (CameraManager.m_cameraManager == null) {
      CameraManager.m_cameraManager = new CameraManager(CameraManager.m_objInit);
      CameraManager.m_objInit = {};
      CameraManager.GetInstance().setLooAtMesh();

      Communicator.reciveEvent(CommunicatorEvent.CHANGE_CAMERA_MODE, (_data) => {
        // console.log(_vars);
        CameraManager.cameraMode = _data;
        CameraManager.aniFlow = null;
      });
    }
    return CameraManager.m_cameraManager;
  }

  /**
   *===============================================================================
   * set cameraMode()      :    setter
   * @param _mode      :   ///
   *===============================================================================*/
  static set cameraMode(_mode) {
    CameraManager.oldMode = CameraManager.currentMode;
    CameraManager.currentMode = _mode;
    //console.log("cameraMode ::: ",_mode);
    switch (_mode) {
      case CameraManager.PLAY_MODE:
        CameraManager.GetInstance().playMode();
        break;
      case CameraManager.MINI_MAP_MODE:
        CameraManager.GetInstance().miniMapMode();
        break;
      case CameraManager.BOOTH_MODE:
        CameraManager.GetInstance().boothMode();
        break;
      default:
        break;
    }
  }

  /**
   *===============================================================================
   * get cameraMode()      :    getter
   * @return               :    ///
   *===============================================================================*/
  static get cameraMode() {
    return CameraManager.currentMode;
  }

  /**
   *===============================================================================
   * get lookAt()      :    getter
   * @return           :   
   *===============================================================================*/
  static get lookAt() {
    return CameraManager.meshLookAt;
  }

  /**
   *===============================================================================
   * setLooAtMesh()    :   
   *===============================================================================*/
  setLooAtMesh() {
    // CameraManager.meshLookAt = BABYLON.MeshBuilder.CreateBox("cube", { size: 1.1, height: 1.1 }, CameraManager.scene);
    // CameraManager.meshLookAt.isVisible = false;
    CameraManager.GetInstance().setCamera();
  }

  /**
   *===============================================================================
   * setCamera()    :   
   *===============================================================================*/
  setCamera() {
    CameraManager.camera = new BABYLON.ArcRotateCamera(
      "Camera",
      -Math.PI / 2, //    
      Math.PI / 4, //
      10, // 
      new BABYLON.Vector3.Zero(),
      Constants.SCENE
    );
    CameraManager.camera.attachControl(Constants.CANVAS, true);

    this.setLimit();
    // var defaultPipeline = new BABYLON.DefaultRenderingPipeline("default", true, Constants.SCENE, [CameraManager.camera]);
    // defaultPipeline.bloomEnabled = true;
    // defaultPipeline.fxaaEnabled = true;
    // defaultPipeline.bloomWeight = 0.5;
    // defaultPipeline.cameraFov = CameraManager.camera.fov;

    //   var lensEffect = new BABYLON.LensRenderingPipeline('lens', {
    //     edge_blur: 1.0,
    //     chromatic_aberration: 1.0,
    //     distortion: 1.0,
    //     dof_focus_distance: 150,
    //     dof_aperture: 6.0,			// set this very high for tilt-shift effect
    //     grain_amount: 1.0,
    //     dof_pentagon: true,
    //     dof_gain: 1.0,
    //     dof_threshold: 1.0,
    //     dof_darken: 0.25
    // }, Constants.SCENE, 1.0, CameraManager.camera);
  }

  /**
   *===============================================================================
   * setCamera()    :     
   *===============================================================================*/
  setLimit() {
    //   
    // CameraManager.camera.lowerAlphaLimit =BABYLON.Tools.ToRadians(0); //  
    // CameraManager.camera.upperAlphaLimit =BABYLON.Tools.ToRadians(360); //  
    CameraManager.camera.lowerBetaLimit = BABYLON.Tools.ToRadians(80); //  
    CameraManager.camera.upperBetaLimit = BABYLON.Tools.ToRadians(80); //   
    CameraManager.camera.lowerRadiusLimit = 7;
    CameraManager.camera.upperRadiusLimit = 10;
    CameraManager.camera.panningSensibility = 0;
  }

  /**
   *===============================================================================
   * miniMapMode()    :    
   *===============================================================================*/
  miniMapMode() {
    gsap.killTweensOf(CameraManager.meshLookAt);
    gsap.killTweensOf(CameraManager.camera);

    if (Constants.SNOW_PARTICLE) Constants.SNOW_PARTICLE.start();

    CameraManager.camera.setTarget(CameraManager.meshLookAt);

    Constants.SCENE.onBeforeAnimationsObservable.remove(CameraManager.aniFlow);

    CameraManager.onFloatingMap();
    CameraManager.GetInstance().setLimit();

    CameraManager.locator.StartFlow();
    CameraManager.camera.minZ = 0.1;
    const endTween = () => {
      if (CameraManager.currentMode !== CameraManager.MINI_MAP_MODE) return;
      CameraManager.meshLookAt.position = new BABYLON.Vector3(Constants.MAP_CENTER.x, Constants.MAP_CENTER.y, Constants.MAP_CENTER.z);

      //  
      CameraManager.camera.lowerBetaLimit = BABYLON.Tools.ToRadians(45); //  
      CameraManager.camera.lowerRadiusLimit = 150; //  
      CameraManager.camera.upperRadiusLimit = 150; //  
    };

    gsap.to(CameraManager.meshLookAt.position, {
      duration: 1,
      x: Constants.MAP_CENTER.x,
      y: Constants.MAP_CENTER.y,
      z: Constants.MAP_CENTER.z,
      ease: "power1.outout",
      onComplete: endTween,
    });

    gsap.to(CameraManager.camera, {
      duration: 1,
      radius: 150,
      beta: BABYLON.Tools.ToRadians(75),
      alpha: BABYLON.Tools.ToRadians(45),
      ease: "power1.outout",
    });
  }

  /**
   *===============================================================================
   * playMode()    :    
   *===============================================================================*/
  playMode() {
    gsap.killTweensOf(CameraManager.meshLookAt);
    gsap.killTweensOf(CameraManager.camera);

    //if(Constants.SNOW_PARTICLE)Constants.SNOW_PARTICLE.stop();

    if (CameraManager.meshTarget === null || CameraManager.meshTarget === undefined) return;
    let nDuration = CameraManager.oldMode === CameraManager.BOOTH_MODE ? 0.7 : 1;

    CameraManager.m_iSwingCountFrame = 0;
    CameraManager.stopFloatingMap();

    CameraManager.GetInstance().setLimit();

    const endTween = () => {
      if (CameraManager.currentMode !== CameraManager.PLAY_MODE) return;
      CameraManager.locator.StopFlow();
      CameraManager.camera.lowerAlphaLimit = BABYLON.Tools.ToRadians(45); //  
      CameraManager.camera.upperAlphaLimit = BABYLON.Tools.ToRadians(115); //  
      CameraManager.camera.lowerBetaLimit = BABYLON.Tools.ToRadians(15); //  
      CameraManager.camera.upperBetaLimit = BABYLON.Tools.ToRadians(80); //   
      CameraManager.camera.lowerRadiusLimit = 15; //  
      CameraManager.camera.upperRadiusLimit = 15; //  
      CameraManager.camera.setTarget(CameraManager.meshTarget);
      if (CameraManager.aniFlow === null) CameraManager.aniFlow = Constants.SCENE.onBeforeAnimationsObservable.add(CameraManager.GetInstance().flowAvatar);
    };

    gsap.to(CameraManager.meshLookAt.position, {
      duration: nDuration,
      x: CameraManager.meshTarget.position.x,
      y: CameraManager.meshTarget.position.y,
      z: CameraManager.meshTarget.position.z,
      ease: "power1.outout",
      onComplete: endTween,
    });

    gsap.to(CameraManager.camera, {
      duration: nDuration,
      radius: 15,
      beta: BABYLON.Tools.ToRadians(45),
      alpha: BABYLON.Tools.ToRadians(90),
      ease: "power1.outout",
    });

    //   
    gsap.to(Constants.CONTAINER_MAP.rotation, {
      duration: nDuration,
      x: 0,
      y: 0,
      z: 0,
      ease: "power1.outout",
    });
  }

  static SetCameraTargetChest() {
    let camera = CameraManager.camera;
    let scene = Constants.SCENE;

    AnimationManager.FlipCylinder();

    let targetPosition = null;

    scene.registerBeforeRender(function SetTarget() {
      if (!CameraManager.isPedstalLanding) {
        targetPosition = AnimationManager.pedestal2.position.add(new BABYLON.Vector3(1, 0, 0));
        camera.setTarget(targetPosition);
      } else {
        CameraManager.camera.position = new BABYLON.Vector3(0,1,0);
        scene.unregisterBeforeRender(SetTarget);
        CameraManager.camera.lowerBetaLimit = BABYLON.Tools.ToRadians(-90); //  
        CameraManager.camera.upperBetaLimit = BABYLON.Tools.ToRadians(90); //   
        CameraManager.camera.lowerAlphaLimit = BABYLON.Tools.ToRadians(-90); //  
        CameraManager.camera.upperAlphaLimit = BABYLON.Tools.ToRadians(-90); //  

        camera.setTarget(new BABYLON.Vector3.Zero());
        camera.alpha = -Math.PI / 2;
        camera.beta = 1.2;
        camera.lowerRadiusLimit = 15; //  
        camera.upperRadiusLimit = 15; //  

        ContentsManager.ContentsPopUp();
        camera.panningSensibility = 0;
      }
    });

    setTimeout(() => {
      Constants.CHEST.dispose();
    }, 500);
  }
}
