import * as BABYLON from "babylonjs";
import gsap from "gsap";
import Communicator from "../common/Communicator";
import Constants from "../common/Constants";
import CommunicatorEvent from "../common/event/CommunicatorEvent";

export default class ParticleManager {
  static particleSystem = null;
  static sparkParticleSystem = null;

  /**
   *===============================================================================
   * constructor()      :
   * @param _objInit   :    
   *===============================================================================*/
  constructor(_objInit) {
    //super();
    if (_objInit !== ParticleManager.m_objInit) {
      throw new Error("ParticleManager     - ParticleManager.GetInstance()");
      // return;
    }
  }

  /**
   *===============================================================================
   * dispose()               :
   *===============================================================================*/
  static dispose() {
    ParticleManager.m_particleManager = null;
    ParticleManager.m_objInit = null;
  }

  /**
   *===============================================================================
   * GetInstance()      :
   * @return            :  
   *===============================================================================*/
  static GetInstance() {
    if (ParticleManager.m_particleManager == null) {
      ParticleManager.m_particleManager = new ParticleManager(ParticleManager.m_objInit);
      ParticleManager.m_objInit = {};
      ParticleManager.GetInstance();
    }
    return ParticleManager.m_particleManager;
  }

  static ParticleSpark() {
    var noiseTexture = new BABYLON.NoiseProceduralTexture("perlin", 256, Constants.SCENE);
    noiseTexture.animationSpeedFactor = 5;
    noiseTexture.persistence = 2;
    noiseTexture.brightness = 0.5;
    noiseTexture.octaves = 6;

    ParticleManager.sparkParticleSystem = new BABYLON.ParticleSystem("sparkParticleSystem", 1000, Constants.SCENE);
    ParticleManager.sparkParticleSystem.particleTexture = new BABYLON.Texture("https://models.babylonjs.com/Demos/weaponsDemo/textures/sparks.png", Constants.SCENE);
    ParticleManager.sparkParticleSystem.minSize = 0.1;
    ParticleManager.sparkParticleSystem.maxSize = 0.2;
    // sparkParticleSystem.particleEmitterType = sparkEmitter;
    var hemisphericEmitter = ParticleManager.sparkParticleSystem.createHemisphericEmitter(8);
    // hemisphericEmitter.radiusRange = 2;

    ParticleManager.sparkParticleSystem.emitter = Constants.SCENE.getNodeByName("Object_8");
    ParticleManager.sparkParticleSystem.minLifeTime = 0.3;
    ParticleManager.sparkParticleSystem.maxLifeTime = 0.7;
    ParticleManager.sparkParticleSystem.emitRate = 30;
    ParticleManager.sparkParticleSystem.addColorGradient(0.0, new BABYLON.Color4(0.9245, 0.654, 0.0915, 0));
    ParticleManager.sparkParticleSystem.addColorGradient(0.04, new BABYLON.Color4(0.9062, 0.6132, 0.0942, 0.1));
    ParticleManager.sparkParticleSystem.addColorGradient(0.4, new BABYLON.Color4(0.97, 0.92, 0.21));
    ParticleManager.sparkParticleSystem.addColorGradient(0.7, new BABYLON.Color4(0.85, 0.46, 0.15));
    ParticleManager.sparkParticleSystem.addColorGradient(0.9, new BABYLON.Color4(0.3113, 0.0367, 0.0367, 0.6));
    ParticleManager.sparkParticleSystem.addColorGradient(1.0, new BABYLON.Color4(0.3113, 0.0367, 0.0367, 0));
    ParticleManager.sparkParticleSystem.blendMode = BABYLON.ParticleSystem.BLENDMODE_ADD;
    ParticleManager.sparkParticleSystem.gravity = new BABYLON.Vector3(0, 5, 0);
    ParticleManager.sparkParticleSystem.noiseTexture = noiseTexture;
    ParticleManager.sparkParticleSystem.noiseStrength = new BABYLON.Vector3(6, 6, 4);
    ParticleManager.sparkParticleSystem.minEmitPower = 1;
    ParticleManager.sparkParticleSystem.maxEmitPower = 2;
    ParticleManager.sparkParticleSystem.updateSpeed = 1 / 200;
    ParticleManager.sparkParticleSystem.start();
  }

  /**
   *===============================================================================
   * set ParticleSunflower()      :  Meta4  -  
   *===============================================================================*/
  static ParticleSunflower(_vec3) {
    ParticleManager.particleSystem = new BABYLON.ParticleSystem("particles", 200, Constants.SCENE);

    ParticleManager.particleSystem.particleTexture = new BABYLON.Texture("images/sunflower.png", Constants.SCENE);

    let startSphere = new BABYLON.Mesh.CreateSphere("Shoot", 4, 1, Constants.SCENE);
    startSphere.position = new BABYLON.Vector3(_vec3.x, _vec3.y, -3);
    startSphere.isVisible = false;

    ParticleManager.particleSystem.emitter = startSphere; // the starting location
    ParticleManager.particleSystem.color1 = new BABYLON.Color4(1, 1, 1, 1);
    ParticleManager.particleSystem.color2 = new BABYLON.Color4(1, 1, 1, 1);
    ParticleManager.particleSystem.colorDead = new BABYLON.Color3.White();
    ParticleManager.particleSystem.minSize = 0.5;
    ParticleManager.particleSystem.maxSize = 1;
    ParticleManager.particleSystem.minLifeTime = 0.1;
    ParticleManager.particleSystem.maxLifeTime = 0.5;
    ParticleManager.particleSystem.emitRate = 50;
    ParticleManager.particleSystem.createSphereEmitter(2);
    ParticleManager.particleSystem.blendMode = BABYLON.ParticleSystem.BLENDMODE_STANDARD;
    ParticleManager.particleSystem.minEmitPower = 3;
    ParticleManager.particleSystem.maxEmitPower = 5;
    ParticleManager.particleSystem.updateSpeed = 0.005;
    ParticleManager.particleSystem.disposeOnStop = true;
    // ParticleManager.particleSystem.renderingGroupId = 1;
    ParticleManager.particleSystem.targetStopDuration = 0.1;
    ParticleManager.particleSystem.start();
  }

  /**
   *===============================================================================
   * set ParticleCoffee()      :  Meta4  -  
   *===============================================================================*/
  static ParticleCoffee(_vec3) {
    // Create a particle system
    ParticleManager.particleSystem = new BABYLON.ParticleSystem("particles", 200, Constants.SCENE);

    //Texture of each particle
    ParticleManager.particleSystem.particleTexture = new BABYLON.Texture("images/coffeeBean.png", Constants.SCENE);

    let startSphere = new BABYLON.Mesh.CreateSphere("Shoot", 4, 1, Constants.SCENE);
    startSphere.position = new BABYLON.Vector3(_vec3.x, _vec3.y, -3);
    startSphere.isVisible = false;

    ParticleManager.particleSystem.emitter = startSphere;
    ParticleManager.particleSystem.color1 = new BABYLON.Color4(1, 1, 1, 1);
    ParticleManager.particleSystem.color2 = new BABYLON.Color4(1, 1, 1, 1);
    ParticleManager.particleSystem.colorDead = new BABYLON.Color3.White();
    ParticleManager.particleSystem.minSize = 0.5;
    ParticleManager.particleSystem.maxSize = 1;
    ParticleManager.particleSystem.minLifeTime = 0.1;
    ParticleManager.particleSystem.maxLifeTime = 0.5;
    ParticleManager.particleSystem.emitRate = 50;
    ParticleManager.particleSystem.createSphereEmitter(2);
    ParticleManager.particleSystem.blendMode = BABYLON.ParticleSystem.BLENDMODE_STANDARD;
    ParticleManager.particleSystem.minEmitPower = 3;
    ParticleManager.particleSystem.maxEmitPower = 5;
    ParticleManager.particleSystem.updateSpeed = 0.005;
    ParticleManager.particleSystem.disposeOnStop = true;
    ParticleManager.particleSystem.targetStopDuration = 0.1;
    ParticleManager.particleSystem.start();
  }
}
