import * as BABYLON from "babylonjs";
import gsap from "gsap";
import Communicator from "../common/Communicator";
import Constants from "../common/Constants";
import CommunicatorEvent from "../common/event/CommunicatorEvent";
import CameraManager from "./CameraManager";
import ContentsManager from "./ContentsManager";

export default class AnimationManager {
  static pedestal;
  static pedestal2;
  static pedestal3;
  static pedestal4;

  /**
   *===============================================================================
   * constructor()      :
   * @param _objInit   :    
   *===============================================================================*/
  constructor(_objInit) {
    //super();

    if (_objInit !== AnimationManager.m_objInit) {
      throw new Error("AnimationManager     - AnimationManager.GetInstance()");
      // return;
    }
  }

  /**
   *===============================================================================
   * dispose()               :
   *===============================================================================*/
  static dispose() {
    gsap.killTweensOf(AnimationManager.meshLookAt);
    gsap.killTweensOf(AnimationManager.camera);

    AnimationManager.m_iSwingCountFrame = 0;

    AnimationManager.meshTarget = null;
    AnimationManager.camera.dispose();
    AnimationManager.m_AnimationManager = null;
    AnimationManager.m_objInit = null;
  }

  /**
   *===============================================================================
   * GetInstance()      :
   * @return            :  
   *===============================================================================*/
  static GetInstance() {
    if (AnimationManager.m_AnimationManager == null) {
      AnimationManager.m_AnimationManager = new AnimationManager(AnimationManager.m_objInit);
      AnimationManager.m_objInit = {};
    }
    return AnimationManager.m_AnimationManager;
  }

  static FlipCylinder() {
    let scene = Constants.SCENE;

    AnimationManager.pedestal = BABYLON.MeshBuilder.CreateCylinder("cylinder", { diameter: 2, height: 0.1, diameter: 1.5 }, scene);
    AnimationManager.pedestal2 = BABYLON.MeshBuilder.CreateCylinder("cylinder2", { diameter: 2, height: 0.1, diameter: 1.5 }, scene);
    AnimationManager.pedestal3 = BABYLON.MeshBuilder.CreateCylinder("cylinder3", { diameter: 2, height: 0.1, diameter: 1.5 }, scene);
    AnimationManager.pedestal4 = BABYLON.MeshBuilder.CreateCylinder("cylinder4", { diameter: 2, height: 0.1, diameter: 1.5 }, scene);

    AnimationManager.pedestal.material = new BABYLON.PBRMaterial("M_Cylinder", scene);
    AnimationManager.pedestal2.material = new BABYLON.PBRMaterial("M_Cylinder2", scene);
    AnimationManager.pedestal3.material = new BABYLON.PBRMaterial("M_Cylinder3", scene);
    AnimationManager.pedestal4.material = new BABYLON.PBRMaterial("M_Cylinder4", scene);

    AnimationManager.pedestal.material.roughness = 0.95;
    AnimationManager.pedestal.material.transparencyMode = 1;
    AnimationManager.pedestal2.material.roughness = 0.95;
    AnimationManager.pedestal2.material.transparencyMode = 1;
    AnimationManager.pedestal3.material.roughness = 0.95;
    AnimationManager.pedestal3.material.transparencyMode = 1;
    AnimationManager.pedestal4.material.roughness = 0.95;
    AnimationManager.pedestal4.material.transparencyMode = 1;

    AnimationManager.pedestal.material.albedoTexture = new BABYLON.Texture("images/grad1.png", scene);
    AnimationManager.pedestal2.material.albedoTexture = new BABYLON.Texture("images/grad2.png", scene);
    AnimationManager.pedestal3.material.albedoTexture = new BABYLON.Texture("images/grad3.png", scene);
    AnimationManager.pedestal4.material.albedoTexture = new BABYLON.Texture("images/grad4.png", scene);

    var frameRate = 15;

    var ySlide = new BABYLON.Animation("ySlide", "position.y", frameRate, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CYCLE);

    var xSlide = new BABYLON.Animation("xSlide", "position.x", frameRate, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CYCLE);
    var xSlide2 = new BABYLON.Animation("xSlide2", "position.x", frameRate, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CYCLE);
    var xSlide3 = new BABYLON.Animation("xSlide3", "position.x", frameRate, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CYCLE);
    var xSlide4 = new BABYLON.Animation("xSlide4", "position.x", frameRate, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CYCLE);

    var keyFramesPY = [];

    var keyFramesPX = [];
    var keyFramesPX2 = [];
    var keyFramesPX3 = [];
    var keyFramesPX4 = [];

    // position y
    keyFramesPY.push({ frame: 0, value: 0 });
    keyFramesPY.push({ frame: frameRate * 0.3, value: 10 });
    keyFramesPY.push({ frame: frameRate * 0.6, value: 17 });
    keyFramesPY.push({ frame: frameRate * 0.9, value: 17 });
    keyFramesPY.push({ frame: frameRate, value: 0 });

    // position x 1
    keyFramesPX.push({ frame: 0, value: 0 });
    keyFramesPX.push({ frame: frameRate * 0.9, value: -3 });
    keyFramesPX.push({ frame: frameRate, value: 2 });

    // position x 2
    keyFramesPX2.push({ frame: 0, value: 0 });
    keyFramesPX2.push({ frame: frameRate * 0.9, value: -1 });
    keyFramesPX2.push({ frame: frameRate, value: 2 });

    // position x 3
    keyFramesPX3.push({ frame: 0, value: 0 });
    keyFramesPX3.push({ frame: frameRate * 0.9, value: 1 });
    keyFramesPX3.push({ frame: frameRate, value: 2 });

    // position x 3
    keyFramesPX4.push({ frame: 0, value: 0 });
    keyFramesPX4.push({ frame: frameRate * 0.9, value: 3 });
    keyFramesPX4.push({ frame: frameRate, value: 2 });

    ySlide.setKeys(keyFramesPY);

    xSlide.setKeys(keyFramesPX);
    xSlide2.setKeys(keyFramesPX2);
    xSlide3.setKeys(keyFramesPX3);
    xSlide4.setKeys(keyFramesPX4);

    var yRot = new BABYLON.Animation("zRot", "rotation.z", frameRate, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CYCLE);

    var keyFramesR = [];

    keyFramesR.push({ frame: 0, value: 0 });
    keyFramesR.push({ frame: frameRate * 0.5, value: 4 * Math.PI });
    keyFramesR.push({ frame: frameRate, value: 8 * Math.PI });

    yRot.setKeys(keyFramesR);

    var animatable = scene.beginDirectAnimation(AnimationManager.pedestal, [ySlide, xSlide, yRot], 0, 2 * frameRate, false);
    var animatable = scene.beginDirectAnimation(AnimationManager.pedestal2, [ySlide, xSlide2, yRot], 0, 2 * frameRate, false);
    var animatable = scene.beginDirectAnimation(AnimationManager.pedestal3, [ySlide, xSlide3, yRot], 0, 2 * frameRate, false);
    var animatable = scene.beginDirectAnimation(AnimationManager.pedestal4, [ySlide, xSlide4, yRot], 0, 2 * frameRate, false);

    animatable.onAnimationEnd = function () {
      AnimationManager.pedestal.dispose();
      AnimationManager.pedestal2.dispose();
      AnimationManager.pedestal3.dispose();
      AnimationManager.pedestal4.dispose();

      Constants.PEDESTAL.getChildMeshes()[0].setEnabled(true);
      Constants.PEDESTAL2.getChildMeshes()[0].setEnabled(true);
      Constants.PEDESTAL3.getChildMeshes()[0].setEnabled(true);
      Constants.PEDESTAL4.getChildMeshes()[0].setEnabled(true);

      Constants.PEDESTAL.getChildMeshes()[0].material.albedoTexture = new BABYLON.Texture("images/grad1.png", scene);
      Constants.PEDESTAL2.getChildMeshes()[0].material.albedoTexture = new BABYLON.Texture("images/grad2.png", scene);
      Constants.PEDESTAL3.getChildMeshes()[0].material.albedoTexture = new BABYLON.Texture("images/grad3.png", scene);
      Constants.PEDESTAL4.getChildMeshes()[0].material.albedoTexture = new BABYLON.Texture("images/grad4.png", scene);

      CameraManager.isPedstalLanding = true;
      ContentsManager.GetInstance();
    };
  }
}
