import React, { useEffect, useRef, useState } from "react";
import styled from "styled-components";

import Communicator from "../../common/Communicator";
import CommunicatorEvent from "../../common/event/CommunicatorEvent";
import Constants_ui from "../common/Constants_ui";
import { Desktop, Mobile, MobileIphone, Tablet } from "../common/MediaQuery";
import { Block, ContainerBox, TextBox } from "../common/StyleMasterComp";
import Utils_ui from "../common/Utils_ui";

import ChatingBox from "../components/ChatingBox";
import CompanyListBox from "../components/CompanyListBox";

import MainDefaultMenu from "../components/MainDefaultMenu";
import MainWorldMenu from "../components/MainWorldMenu";

// import IcoChatOn from '../images/ico_chatOn.png';
// import ModalCatalog from "../modals/ModalCatalog";
// import ModalMovie from "../modals/ModalMovie";

import ReactGA from 'react-ga';

let BtnChatOn = styled.div`
    pointer-events:all;
    cursor: pointer;
    width:40px;
    height:40px;
    background-image: url(${props => props.ImgURL});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    position:absolute; bottom: 20px; left: 26px;
    z-index:10;
`;

let ChatBoxContainer = styled.div`
    position: absolute;
    left: 26px;
    //bottom: calc(0vh + 20px);
    bottom:20px;
    `;


let ChatdataDefault = [
    { roomType: "Room_Type_Normal", messageType: "chatting", name: "UOS", msg: ",    ." }
    , { roomType: "Room_Type_Lobby", messageType: "chatting", name: "UOS", msg: ",    ." }
]


function SendAnalyticsEvent(strType,strCate,strAction){
    if(strType==="page"){
        ReactGA.pageview(strCate);
    }else if(strType==="modal"){
        ReactGA.modalview(strCate);
    }else if(strType==="event"){
        ReactGA.event({
            category: strCate,
            action: strAction
            // category: 'ChatRoom',
            // action: 'Chating Open'
        });
    }
}

function PageMain(props){
    //console.log("",props);

    let [controlMode, setControlMode] = useState("Default");

    let [isOpenCompanyList, setOpenCompanyList] = useState(false);
    let [isUseChatRoom, setUseChatRoom] = useState(false);

    let [isOpenVideo, setOpenVideo] = useState(false);
    let [strVideoUrl, setVideoUrl] = useState('');

    let [isOpenCatalog, setOpenCatalog] = useState(false);
    let [arrCatalogData, setCatalogData] = useState([]);

    let [chatData, setChatData] = useState([]);

    let [iVh, setiVh] = useState(1);


    const sampleRef = useRef({});

    const [audio] = useState(new Audio('./sound/Netstream_Master.mp3'));
    const [playing, setPlaying] = useState(false);

    useEffect(() => {
        playing ? audio.play() : audio.pause();
        audio.loop = true;
        audio.volume=0.1;
    },
        [playing]
    );

    useEffect(() => {
        audio.addEventListener('ended', () => setPlaying(true));
        return () => {
            audio.removeEventListener('ended', () => setPlaying(true));
        };
    }, [playing]);

    useEffect(() => {
        setChatData(ChatdataDefault)
    }, [chatData])


    function handleVisibilityChange() {
        if (document.hidden) {
            // the page is hidden
            setPlaying(false);
        } else {
            // the page is visible
            setPlaying(true);
        }
    }

    useEffect(() => {
        document.addEventListener("visibilitychange", handleVisibilityChange, false);
    }, [])

    

    let onOpenVideo = (_strVars) => {
        //console.log('  URL:', _strVars);
        if (_strVars !== "") {
            setVideoUrl(_strVars);
            setOpenVideo(true);
            setPlaying(false)
        }
    }

    let onOpenCatalog = (_strVars) => {
        //console.log('  URL:', _strVars);
        if (_strVars !== "") {
            setCatalogData(_strVars);
            setOpenCatalog(true);
        }
    }

    let onOpenUrl = (_strUrl) => {
        //console.log("Url,PDF  URL:", _strUrl);
        if (_strUrl !== "") {
            window.open(_strUrl, '_blank');
            SendAnalyticsEvent("event","openUrl",_strUrl);
        }
    }

    let onOpenTotalUrl = (_strType,_strUrl) =>{
        if (_strUrl !== "") {
            window.open(_strUrl, '_blank');
            ReactGA.event({
                category: _strType,
                action: "Open PDF",
                label:_strUrl
            });
        }
    }
    
    let onOpenPDFUrl=(_strUrl)=>{
        onOpenTotalUrl("PDF",_strUrl)
    }
    
    function updateChatData(_objVar) {
        ChatdataDefault = [...ChatdataDefault, { roomType: _objVar.roomType, messageType: _objVar.messageType, name: _objVar.name, msg: _objVar.msg }];
        setChatData(ChatdataDefault);
    }

    function onOpenBoardVideo(_objVar) {
        console.log("onOpenBoardVideo :",_objVar);
        if(_objVar.boardType==="A"){
            ReactGA.event({
                category:"  "
                ,action:" "
                ,label:" "
            }); 
        }else if(_objVar.boardType==="B"){
            ReactGA.event({
                category:" "
                ,action:" "
                ,label:" "
            }); 
        }
        onOpenVideo(_objVar.url);
    }


    function sendTenant(_objVar){
        Communicator.sendEvent(CommunicatorEvent.GO_TO_TENANT, { id: _objVar.id });
        setOpenCompanyList(false)
        setControlMode("Default");
    }

    function sendChatData(_objVar){
        if (_objVar.menuIndex === 0){
            Communicator.sendEvent(CommunicatorEvent.SEND_CHAT_MESSAGE, 
                { roomType: "Room_Type_Normal", messageType: "chatting", msg: _objVar.msg });
            // updateChatData({ roomType: "Room_Type_Normal", messageType: "chatting", msg: _objVar.msg, name:"    "})
        }else {
            Communicator.sendEvent(CommunicatorEvent.SEND_CHAT_MESSAGE,
                { roomType: "Room_Type_Lobby", messageType: "chatting", msg: _objVar.msg });
        }
    }

    useEffect(() => {
        ReactGA.pageview("MainWorld");
        console.log("MainWorld")
    },[ReactGA])

    useEffect(() => {
        Communicator.reciveEvent(CommunicatorEvent.OPEN_VIDEO, onOpenVideo);
        Communicator.reciveEvent(CommunicatorEvent.OPEN_CATALOG, onOpenCatalog);
        Communicator.reciveEvent(CommunicatorEvent.OPEN_URL_LINK, onOpenUrl);
        Communicator.reciveEvent(CommunicatorEvent.OPEN_PDF, onOpenPDFUrl);
        Communicator.reciveEvent(CommunicatorEvent.RECEIVE_CHAT_MESSAGE, updateChatData);
        Communicator.reciveEvent(CommunicatorEvent.OPEN_BOARDVIDEO, onOpenBoardVideo);
        setPlaying(true);
        
    }, [])

    return(
        <ContainerBox>
            <Block ContainerHeight={Utils_ui.PercentToPx(props.nVh,100)} Display="flex" >
                {
                    (controlMode === "Default")
                    ?<>
                        <MainDefaultMenu funcControlMode={setControlMode}/>
                        <BtnChatOn ImgURL={""} onClick={()=>{
                            //   
                            setUseChatRoom(true);
                            //SendAnalyticsEvent('event','ChatRoom','Chating Open');

                            ReactGA.event({
                                category: 'Chatting',
                                action: 'ChatRoom',
                                label:'Open'
                            });
                        }}/>
                    </>
                    : <MainWorldMenu funcControlMode={setControlMode} funcSetCompanyList={setOpenCompanyList} />
                }
                
                <ChatBoxContainer>
                    {(isUseChatRoom && controlMode === "Default")? 
                        <ChatingBox
                            funcUseChatRoom={setUseChatRoom}
                            funcSendChatData={sendChatData}
                            arrChatdata={ChatdataDefault}
                            funcSetChatData={setChatData}
                            sampleRef={sampleRef} />  : null}
                </ChatBoxContainer>
                
                {isOpenCompanyList ? <CompanyListBox 
                                    funcSetCompanyList={setOpenCompanyList}
                                    funcSendTenant={sendTenant}
                                    arrCompanyData={props.arrCompanyData}
                                    funcSendAnalyticsEvent={SendAnalyticsEvent} /> : null}
            </Block>
        </ContainerBox>
    )
}

export default PageMain;