// import react from 'react';
import styled from "styled-components";

import Communicator from "../../common/Communicator";
import CommunicatorEvent from "../../common/event/CommunicatorEvent";

// import IcoWorldMap from '../images/ico_worldMap.png';
// import IcoYoutube from '../images/ico_youtube.png';
// import IcoWebinar from '../images/ico_Webinar.png';

import ReactGA from 'react-ga'

let ContainerBox = styled.div`
    pointer-events:none;   
    height:100vh;
    width:100vw;
    list-style-type: none; margin: 0; padding: 0;
`;

let BtnIco = styled.div`
    pointer-events:all;
    cursor: pointer;
    background-image: url(${props => props.ImgURL});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
`;

let BtnWorldMap = styled(BtnIco)`
    position:absolute; left: 26px; top: 20px; float: left; width:92px; height:99px;
    @media screen and (max-width: 500px) {
        left: 16px; top: 15px; width:69px; height:73px;
    }
`;

let BtnYoutube = styled(BtnIco)`
    /* position:absolute; right: 106px; top: 20px; float: right; width:50px; height:50px; */
    position:absolute; right: 26px; top: 20px; float: right; width:50px; height:50px;
    @media screen and (max-width: 500px) {
        /* right: 70px; top: 15px; width:40px; height:40px; */
        right: 16px; top: 15px; width:40px; height:40px;
    }
`;
let BtnWebinar = styled(BtnIco)`
    position:absolute; right: 26px; top: 20px; float: right; width:50px; height:50px;
    @media screen and (max-width: 500px) {
        right: 16px; top: 15px; width:40px; height:40px;
    }
`;

function MainDefaultMenu(props){

    return(
        <ContainerBox>
            {/* <BtnWorldMap ImgURL={IcoWorldMap} onClick={() => {
                Communicator.sendEvent(CommunicatorEvent.CHANGE_CAMERA_MODE, "Mini_Map_Mode");
                props.funcControlMode("World");
            }} />
            <BtnYoutube ImgURL={IcoYoutube} onClick={() => {
                window.open("https://www.youtube.com/user/universityofseoul", '_blank');
            }} /> */}
            {/* <BtnWebinar ImgURL={IcoWebinar} onClick={() => {
                window.open("https://event-us.kr/ibkchanggong/event/38567", '_blank');
            }} /> */}
        </ContainerBox>
    )
}

export default MainDefaultMenu;