import React from "react";
import styled from "styled-components";
import Utils_ui from "../common/Utils_ui";
import {Desktop, Mobile, MobileIphone, Tablet} from "../common/MediaQuery";
import {ContainerBox, Block, TextBox} from "../common/StyleMasterComp";

let Progress = styled.div`
    position: absolute;
    bottom: 0px;
    background-color: white;
    width: ${props => props.percent}%;
    transition: width 400ms ease-in-out;
    height: 8px;
`;

function LoadingProgressBar(props) {
    return (
        <ContainerBox>

            <Block
                ContainerWidth="100%"
                ContainerHeight={Utils_ui.PercentToPx(props.compHeight, 60)}>
                <Desktop>
                    <TextBox FontSize="30px" FontColor="#fff" FontWeight="200" TextAlign="center" MarginTop="50px">
                        {props.percent}%
                    </TextBox>
                </Desktop>
                <Tablet>
                    <TextBox FontSize="25px" FontColor="#fff" FontWeight="200" TextAlign="center">
                        {props.percent}%
                    </TextBox>
                </Tablet>
                <Mobile>
                    <TextBox FontSize="20px" FontColor="#fff" FontWeight="200" TextAlign="center">
                        {props.percent}%
                    </TextBox>
                </Mobile>
                <MobileIphone>
                    <TextBox FontSize="20px" FontColor="#fff" FontWeight="200" TextAlign="center">
                        {props.percent}%
                    </TextBox>
                </MobileIphone>
                <Progress percent={props.percent}/>
            </Block>
            <Block
                ContainerWidth="100%"
                ContainerHeight={Utils_ui.PercentToPx(props.compHeight, 40)}></Block>
        </ContainerBox>
    );
};

export default LoadingProgressBar;