import React, { useState, useEffect } from "react";
import styled from "styled-components";

import Communicator from "../../common/Communicator";
import CommunicatorEvent from "../../common/event/CommunicatorEvent";
import Constants_ui from "../common/Constants_ui";
import { Desktop, Mobile, MobileIphone, Tablet } from "../common/MediaQuery";
import { Block, ContainerBox, TextBox } from "../common/StyleMasterComp";
import Utils_ui from "../common/Utils_ui";

import LoadingProgressBar from "../components/LoadingProgressBar";

// import ImgLogo from "../images/loading_img_logo.png";
import ImgBG from "../images/UNIT_logo.png";

// let BgBox = styled.div`
//     width:100vw;
//     height:${props => props.Height};
//     background: linear-gradient(#000000 0%, #00245C 53.12%, #005BAD 100%);
//     position: absolute;
//     pointer-events:all;
// `;
let BgBox = styled.div`
  width: 100vw;
  height: ${(props) => props.Height};
  background-image: url(${(props) => props.URL2});
  background-size: cover;
  position: absolute;
  pointer-events: all;
`;

let LogoImg = styled.div`
  position: absolute;
  background-image: url(${(props) => props.URL});
  left: calc(50% - ((${(props) => props.Width} / 2)));
  bottom: 3vh;
  width: ${(props) => props.Width};
  height: 113px;
`;

function PageLoading(props) {
  //console.log("", props);
  let [NumPercent, setNumPercent] = useState(0);

  let [isLoadReady, setIsLoadReady] = useState(false);
  let [isTimeReady, setIsTimeReady] = useState(false);

  let onChangeLoadCount = (_nVars) => {
    let nVars = parseInt(_nVars);

    nVars > 100 ? (nVars = 100) : (nVars = parseInt(_nVars));
    setNumPercent(nVars);
  };

  let onWorldLoadComp = () => {
    // console.log("onWorldLoadComp");
    setIsLoadReady(true);
  };

  useEffect(() => {
    let pageMoveTimer = setTimeout(() => {
      // console.log("setTimeout");
      setIsTimeReady(true);
    }, Constants_ui.FIRSTLOADING_TIME);
    return () => {
      clearTimeout(pageMoveTimer);
    };
  }, []);

  useEffect(() => {
    Communicator.reciveEvent(
      CommunicatorEvent.WORLD_LOADING,
      onChangeLoadCount
    );
    Communicator.reciveEvent(
      CommunicatorEvent.WORLD_LOAD_COMPLETE,
      onWorldLoadComp
    );
  }, []);

  useEffect(() => {
    if (isLoadReady && isTimeReady)
      props.setSelectPageName(Constants_ui.PAGE_AUTH);
  }, [isLoadReady, isTimeReady, props]);

  return (
    <ContainerBox>
      <BgBox
        URL2={ImgBG}
        Height={Utils_ui.PercentToPx(props.nVh, 100)}
      />
      <Block
        ContainerWidth="100vw"
        ContainerHeight={Utils_ui.PercentToPx(props.nVh, 22)}
      />
      <Block ContainerHeight={Utils_ui.PercentToPx(props.nVh, 22)}>
        {/* <LogoImg URL={ImgLogo} Width="115px" /> */}
      </Block>
      <Block ContainerHeight={Utils_ui.PercentToPx(props.nVh, 20)}>
        <LoadingProgressBar
          compHeight={Utils_ui.PercentToPx(props.nVh, 12, false)}
          percent={NumPercent}
        ></LoadingProgressBar>
      </Block>
      <Block ContainerHeight={Utils_ui.PercentToPx(props.nVh, 22)}>
        <Desktop>
          <TextBox
            FontSize="50px"
            FontColor="#fff"
            FontWeight="400"
            MarginTop="-10px"
            MarginBottom="20px"
            TextAlign="center"
          >
            WebGL Portfolio
          </TextBox>
        </Desktop>
        <Tablet>
          <TextBox
            FontSize="45px"
            FontColor="#fff"
            FontWeight="400"
            MarginTop="-5px"
            MarginBottom="20px"
            TextAlign="center"
          >
            WebGL Portfolio
          </TextBox>
        </Tablet>
        <Mobile>
          <TextBox
            FontSize="30px"
            FontColor="#fff"
            FontWeight="400"
            MarginTop="0px"
            MarginBottom="20px"
            TextAlign="center"
          >
            WebGL Portfolio
          </TextBox>
        </Mobile>
        <MobileIphone>
          <TextBox
            FontSize="30px"
            FontColor="#fff"
            FontWeight="400"
            MarginTop="0px"
            MarginBottom="20px"
            TextAlign="center"
          >
            WebGL Portfolio
          </TextBox>
        </MobileIphone>
        {/* <TextBox FontSize="12px" FontColor="#fff" FontWeight="400" MarginTop="0px" TextAlign="center"> 
                isTimeReady : {String(isTimeReady)}, isLoadReady: {String(isLoadReady)}</TextBox> */}
      </Block>
      <Block ContainerHeight={Utils_ui.PercentToPx(props.nVh, 22)} />
    </ContainerBox>
  );
}

export default PageLoading;
