import * as BABYLON from "babylonjs";
import * as GUI from "babylonjs-gui";
import gsap from "gsap";
import Communicator from "../common/Communicator";
import Constants from "../common/Constants";
import CommunicatorEvent from "../common/event/CommunicatorEvent";
import CameraManager from "./CameraManager";
import BasicWorld from "./BasicWorld";
import AnimationManager from "./AnimationManager";

export default class ContentsManager {
  static item1;
  static item2;
  static item3;
  static item4;

  static particle1;
  static particle2;
  static particle3;
  static particle4;

  static particleCountArr = [];
  static UItarget = null;
  static detailGUI = null;
  static detailGUIText = null;
  /**
   *===============================================================================
   * constructor()      :
   * @param _objInit   :    
   *===============================================================================*/
  constructor(_objInit) {
    //super();

    if (_objInit !== ContentsManager.m_objInit) {
      throw new Error("ContentsManager     - ContentsManager.GetInstance()");
      // return;
    }
  }

  /**
   *===============================================================================
   * dispose()               :
   *===============================================================================*/
  static dispose() {
    gsap.killTweensOf(ContentsManager.meshLookAt);
    gsap.killTweensOf(ContentsManager.camera);

    ContentsManager.m_iSwingCountFrame = 0;

    ContentsManager.meshTarget = null;
    ContentsManager.camera.dispose();
    ContentsManager.m_ContentsManager = null;
    ContentsManager.m_objInit = null;
  }

  /**
   *===============================================================================
   * GetInstance()      :
   * @return            :  
   *===============================================================================*/
  static GetInstance() {
    if (ContentsManager.m_ContentsManager == null) {
      ContentsManager.m_ContentsManager = new ContentsManager(ContentsManager.m_objInit);
      ContentsManager.m_objInit = {};
    }
    return ContentsManager.m_ContentsManager;
  }

  static async ContentsPopUp() {
    let scene = Constants.SCENE;
    let engine = Constants.ENGINE;

    ContentsManager.item1.position = new BABYLON.Vector3(-10, 1, 1);
    ContentsManager.item2.position = new BABYLON.Vector3(-2.9, 1, 0);
    ContentsManager.item3.position = new BABYLON.Vector3(3, 1, 1);
    ContentsManager.item4.position = new BABYLON.Vector3(9, 1, 0);

    const frameRate = 2000;
    const ySlide = new BABYLON.Animation("YSlide", "position.y", frameRate, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CYCLE);
    const keyFrames = [];

    keyFrames.push({
      frame: 0,
      value: 1.1,
    });
    keyFrames.push({
      frame: frameRate,
      value: 1,
    });
    keyFrames.push({
      frame: 2 * frameRate,
      value: 1.1,
    });

    ySlide.setKeys(keyFrames);

    const ySlide2 = new BABYLON.Animation("YSlide2", "position.y", frameRate, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CYCLE);
    const keyFrames2 = [];

    keyFrames2.push({
      frame: 0,
      value: 1,
    });
    keyFrames2.push({
      frame: frameRate,
      value: 1.1,
    });
    keyFrames2.push({
      frame: 2 * frameRate,
      value: 1,
    });

    ySlide2.setKeys(keyFrames2);

    scene.beginDirectAnimation(ContentsManager.item1, [ySlide], 0, 2 * frameRate, true);
    scene.beginDirectAnimation(ContentsManager.item2, [ySlide2], 0, 2 * frameRate, true);
    scene.beginDirectAnimation(ContentsManager.item3, [ySlide], 0, 2 * frameRate, true);
    scene.beginDirectAnimation(ContentsManager.item4, [ySlide2], 0, 2 * frameRate, true);

    ContentsManager.item2.rotation = new BABYLON.Vector3(0, BABYLON.Tools.ToRadians(315), 0);
    ContentsManager.item3.rotation = new BABYLON.Vector3(0, Math.PI * 1.5, 0);
    ContentsManager.item4.rotation = new BABYLON.Vector3(0, Math.PI * 0.58, 0);

    BABYLON.ParticleHelper.CreateFromSnippetAsync("2JRD1A#2", scene, false).then((system) => {
      ContentsManager.particleCountArr.push(system);
      ContentsManager.particle1 = system;
      system.isBillboardBased = false;
      system.emitter = new BABYLON.Vector3(-9, 0.3, 1);
      system.preWarmCycles = 100;
      system.preWarmStepOffset = 5;
      system.updateSpeed = 0.01;
      system.removeColorGradient(0.46);
      system.removeColorGradient(0.53);
      system.addColorGradient(0.46, new BABYLON.Color4.FromHexString("#FFCA38FF"), new BABYLON.Color4.FromHexString("#FF8600FF"));
      system.addColorGradient(0.53, new BABYLON.Color4.FromHexString("#FF3D27FF"), new BABYLON.Color4.FromHexString("#FF0000FF"));
    });

    BABYLON.ParticleHelper.CreateFromSnippetAsync("2JRD1A#2", scene, false).then((system) => {
      ContentsManager.particleCountArr.push(system);

      ContentsManager.particle2 = system;
      system.isBillboardBased = false;
      system.emitter = new BABYLON.Vector3(-3, 0.3, 0);
      system.preWarmCycles = 100;
      system.preWarmStepOffset = 5;
      system.updateSpeed = 0.01;
      system.removeColorGradient(0.46);
      system.removeColorGradient(0.53);
      system.addColorGradient(0.46, new BABYLON.Color4.FromHexString("#D04CFFFF"), new BABYLON.Color4.FromHexString("#FF16E0FF"));
      system.addColorGradient(0.53, new BABYLON.Color4.FromHexString("#A886FAFF"), new BABYLON.Color4.FromHexString("#1610FFFF"));
    });

    BABYLON.ParticleHelper.CreateFromSnippetAsync("2JRD1A#2", scene, false).then((system) => {
      ContentsManager.particleCountArr.push(system);

      ContentsManager.particle3 = system;
      system.isBillboardBased = false;
      system.emitter = new BABYLON.Vector3(3, 0.3, 1);
      system.preWarmCycles = 100;
      system.preWarmStepOffset = 5;
      system.updateSpeed = 0.01;
    });

    BABYLON.ParticleHelper.CreateFromSnippetAsync("2JRD1A#2", scene, false).then((system) => {
      ContentsManager.particle4 = system;
      system.isBillboardBased = false;
      system.emitter = new BABYLON.Vector3(9, 0.3, 0);
      system.preWarmCycles = 100;
      system.preWarmStepOffset = 5;
      system.updateSpeed = 0.01;
      system.removeColorGradient(0.46);
      system.removeColorGradient(0.53);
      system.addColorGradient(0.46, new BABYLON.Color4.FromHexString("#A4FF3FFF"), new BABYLON.Color4.FromHexString("#27FF49FF"));
      system.addColorGradient(0.53, new BABYLON.Color4.FromHexString("#CAFA77FF"), new BABYLON.Color4.FromHexString("#E4FC67FF"));
    });

    setTimeout(() => {
      ContentsManager.item1.setEnabled(true);
      ContentsManager.item2.setEnabled(true);
      ContentsManager.item3.setEnabled(true);
      ContentsManager.item4.setEnabled(true);
    }, 300);

    var particleSystem = new BABYLON.ParticleSystem("particles", 1);

    particleSystem.particleTexture = new BABYLON.Texture("https://i.imgur.com/CNc4JIL.png");

    particleSystem.emitter = new BABYLON.Vector3(-9, 0, 0);
    particleSystem.start();

    particleSystem.addSizeGradient(0, 1, 2); //size range at start of particle lifetime
    particleSystem.addSizeGradient(1.0, 3, 5); //size range at end of particle lifetime
    particleSystem.color1 = new BABYLON.Color4(0.15, 0.15, 0.15, 0.2);
    particleSystem.color2 = new BABYLON.Color4(0.15, 0.15, 0.15, 0.2);
    particleSystem.colorDead = new BABYLON.Color4(0, 0, 0, 0);
    particleSystem.updateSpeed = 0.02;
    particleSystem.minEmitBox = new BABYLON.Vector3.Zero();
    particleSystem.maxEmitBox = new BABYLON.Vector3.Zero();
    particleSystem.minScaleX = 0.5;
    particleSystem.minScaleX = 2;
    particleSystem.targetStopDuration = 0.5;
    particleSystem.disposeOnStop = true;
    particleSystem.blendMode = BABYLON.ParticleSystem.BLENDMODE_ONEONE;

    var particleSystem2 = new BABYLON.ParticleSystem("particles2", 1);
    particleSystem2.particleTexture = new BABYLON.Texture("https://i.imgur.com/CNc4JIL.png");

    particleSystem2.emitter = new BABYLON.Vector3(-3, 0, 0);
    particleSystem2.start();

    particleSystem2.addSizeGradient(0, 1, 2); //size range at start of particle lifetime
    particleSystem2.addSizeGradient(1.0, 3, 5); //size range at end of particle lifetime
    particleSystem2.color1 = new BABYLON.Color4(0.15, 0.15, 0.15, 0.2);
    particleSystem2.color2 = new BABYLON.Color4(0.15, 0.15, 0.15, 0.2);
    particleSystem2.colorDead = new BABYLON.Color4(0, 0, 0, 0);
    particleSystem2.updateSpeed = 0.02;
    particleSystem2.minEmitBox = new BABYLON.Vector3.Zero();
    particleSystem2.maxEmitBox = new BABYLON.Vector3.Zero();
    particleSystem2.minScaleX = 0.5;
    particleSystem2.minScaleX = 2;

    particleSystem2.targetStopDuration = 0.5;
    particleSystem2.disposeOnStop = true;
    particleSystem2.blendMode = BABYLON.ParticleSystem.BLENDMODE_ONEONE;

    var particleSystem3 = new BABYLON.ParticleSystem("particles3", 1);
    particleSystem3.particleTexture = new BABYLON.Texture("https://i.imgur.com/CNc4JIL.png");

    particleSystem3.emitter = new BABYLON.Vector3(3, 0, 0);
    particleSystem3.start();

    particleSystem3.addSizeGradient(0, 1, 2); //size range at start of particle lifetime
    particleSystem3.addSizeGradient(1.0, 3, 5); //size range at end of particle lifetime
    particleSystem3.color1 = new BABYLON.Color4(0.15, 0.15, 0.15, 0.2);
    particleSystem3.color2 = new BABYLON.Color4(0.15, 0.15, 0.15, 0.2);
    particleSystem3.colorDead = new BABYLON.Color4(0, 0, 0, 0);
    particleSystem3.updateSpeed = 0.02;
    particleSystem3.minEmitBox = new BABYLON.Vector3.Zero();
    particleSystem3.maxEmitBox = new BABYLON.Vector3.Zero();
    particleSystem3.minScaleX = 0.5;
    particleSystem3.minScaleX = 2;
    particleSystem3.targetStopDuration = 0.5;
    particleSystem3.disposeOnStop = true;
    particleSystem3.blendMode = BABYLON.ParticleSystem.BLENDMODE_ONEONE;

    var particleSystem4 = new BABYLON.ParticleSystem("particles4", 1);
    particleSystem4.particleTexture = new BABYLON.Texture("https://i.imgur.com/CNc4JIL.png");

    particleSystem4.emitter = new BABYLON.Vector3(9, 0, 0);
    particleSystem4.start();

    particleSystem4.addSizeGradient(0, 1, 2); //size range at start of particle lifetime
    particleSystem4.addSizeGradient(1.0, 3, 5); //size range at end of particle lifetime
    particleSystem4.color1 = new BABYLON.Color4(0.15, 0.15, 0.15, 0.2);
    particleSystem4.color2 = new BABYLON.Color4(0.15, 0.15, 0.15, 0.2);
    particleSystem4.colorDead = new BABYLON.Color4(0, 0, 0, 0);
    particleSystem4.updateSpeed = 0.02;
    particleSystem4.minEmitBox = new BABYLON.Vector3.Zero();
    particleSystem4.maxEmitBox = new BABYLON.Vector3.Zero();
    particleSystem4.minScaleX = 0.5;
    particleSystem4.minScaleX = 2;

    particleSystem4.targetStopDuration = 0.5;
    particleSystem4.disposeOnStop = true;
    particleSystem4.blendMode = BABYLON.ParticleSystem.BLENDMODE_ONEONE;

    //  GUI

    let frame = scene.getNodeByName("SM_frame_phong1_0");
    ContentsManager.UItarget = new BABYLON.MeshBuilder.CreatePlane("UITarget", { size: 0.3 }, scene);
    ContentsManager.UItarget.material = new BABYLON.StandardMaterial("UITarget_Mat", scene);
    ContentsManager.UItarget.material.diffuseTexture = new BABYLON.Texture("images/click.png", scene);
    ContentsManager.UItarget.material.diffuseTexture.hasAlpha = true;
    ContentsManager.UItarget.material.alpha = 0;

    var advancedTexture = GUI.AdvancedDynamicTexture.CreateFullscreenUI("UI");
    advancedTexture.useInvalidateRectOptimization = false;

    ContentsManager.detailGUI = new GUI.Rectangle();
    advancedTexture.addControl(ContentsManager.detailGUI);
    ContentsManager.detailGUI.width = "300px";
    ContentsManager.detailGUI.height = "200px";
    ContentsManager.detailGUI.thickness = 2;
    ContentsManager.detailGUI.linkOffsetX = "150px";
    ContentsManager.detailGUI.linkOffsetY = "-100px";
    ContentsManager.detailGUI.transformCenterX = 0;
    ContentsManager.detailGUI.transformCenterY = 1;
    ContentsManager.detailGUI.background = "grey";
    ContentsManager.detailGUI.alpha = 0.7;
    ContentsManager.detailGUI.scaleX = 0;
    ContentsManager.detailGUI.scaleY = 0;
    ContentsManager.detailGUI.cornerRadius = 30;
    ContentsManager.detailGUI.paddingLeft = "3%";
    ContentsManager.detailGUI.linkWithMesh(ContentsManager.UItarget);

    ContentsManager.detailGUIText = new GUI.TextBlock();
    ContentsManager.detailGUIText.text = "";
    ContentsManager.detailGUIText.color = "White";
    ContentsManager.detailGUIText.fontSize = 14;
    ContentsManager.detailGUIText.textWrapping = true;
    ContentsManager.detailGUIText.textVerticalAlignment = GUI.Control.VERTICAL_ALIGNMENT_TOP;
    ContentsManager.detailGUIText.background = "#006994";
    ContentsManager.detailGUI.addControl(ContentsManager.detailGUIText);
    ContentsManager.detailGUIText.alpha = 1 / ContentsManager.detailGUIText.parent.alpha;
    ContentsManager.detailGUIText.paddingTop = "20px";
    ContentsManager.detailGUIText.paddingBottom = "20px";
    ContentsManager.detailGUIText.paddingLeft = "20px";
    ContentsManager.detailGUIText.paddingRight = "20px";

    let itemActionManager = new BABYLON.ActionManager(scene);
    ContentsManager.UItarget.actionManager = itemActionManager;

    let scaleXAnimation = new BABYLON.Animation("myAnimation", "scaleX", 30, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CONSTANT);
    let scaleYAnimation = new BABYLON.Animation("myAnimation", "scaleY", 30, BABYLON.Animation.ANIMATIONTYPE_FLOAT, BABYLON.Animation.ANIMATIONLOOPMODE_CONSTANT);

    var keys = [];

    keys.push({
      frame: 0,
      value: 0,
    });
    keys.push({
      frame: 10,
      value: 1,
    });

    scaleXAnimation.setKeys(keys);
    scaleYAnimation.setKeys(keys);
    ContentsManager.detailGUI.animations = [];
    ContentsManager.detailGUI.animations.push(scaleXAnimation);
    ContentsManager.detailGUI.animations.push(scaleYAnimation);

    itemActionManager.registerAction(
      new BABYLON.ExecuteCodeAction(BABYLON.ActionManager.OnPointerOverTrigger, function (ev) {
        if (Constants.isDetailView) {
          scene.beginAnimation(ContentsManager.detailGUI, 0, 10, false);
        }
      })
    );
    //if hover is over remove highlight of the mesh
    itemActionManager.registerAction(
      new BABYLON.ExecuteCodeAction(BABYLON.ActionManager.OnPointerOutTrigger, function (ev) {
        if (Constants.isDetailView) {
          scene.beginAnimation(ContentsManager.detailGUI, 10, 0, false);
        }
      })
    );
  }
}
