  AnimationControl() {
    let scene = this.scene;

    let anim = this.chestAnim;
    let key_book = this.key_book;

    let isChestOpen = false;
    let isAnimFinish = false;
    let godrays = this.godrays;
    var projectionMatrix = this.scene.activeCamera.getProjectionMatrix();
    var r = projectionMatrix.getRow(3);
    var t = 0;

    let flare00 = null;
    let flare01 = null;

    this.scene.registerBeforeRender(function () {
      if (anim.isPlaying) {
        r.x += Math.cos(t) * 0.08;
        r.y += Math.sin(t) * 0.06;
        projectionMatrix.setRowFromFloats(3, r.x, r.y, r.z, r.w);
        t += 81337.18;

        if (anim.animatables[0].masterFrame >= 4.3) {
          if (!isChestOpen) {
            isChestOpen = !isChestOpen;

            // var lensFlareSystem = new BABYLON.LensFlareSystem(
            //   "lensFlareSystem", //name
            //   scene.getMeshByName("Object_8"), // emitter
            //   scene
            // );

            // flare00 = new BABYLON.LensFlare(
            //   0.5, // size
            //   0, // position
            //   new BABYLON.Color3(1, 1, 1), // color
            //   "images/flare.png", // texture
            //   lensFlareSystem // lens flare system
            // );
            // flare01 = new BABYLON.LensFlare(
            //   0.75,
            //   0.25,
            //   new BABYLON.Color3(0.95, 0.89, 0.71),
            //   "images/flare.png",
            //   lensFlareSystem
            // );

            key_book.setEnabled(false);
            anim.speedRatio = 5;

            const particleSystem = new BABYLON.ParticleSystem("particles", 1);
            particleSystem.particleTexture = new BABYLON.Texture("images/flare.png");

            particleSystem.createPointEmitter(new BABYLON.Vector3.Zero(), new BABYLON.Vector3.Zero());

            particleSystem.minLifeTime = 0.5;
            particleSystem.maxLifeTime = 0.5;

            for (let i = 0; i <= 2; i += 0.05) {
              particleSystem.addSizeGradient(i, 30 * (1 - (i - 1) * (i - 1)));
            }

            particleSystem.disposeOnStop = true;
            particleSystem.targetStopDuration = 0.5;
            particleSystem.updateSpeed = 0.015;

            particleSystem.start();

            particleSystem.addColorGradient(0, new BABYLON.Color4(1, 0.75, 0.07)); //color at start of particle lifetime
            particleSystem.addColorGradient(1, new BABYLON.Color4(0.97, 0.92, 0.7)); //color at end of particle lifetime
          }
        }
        if (anim.animatables[0].masterFrame >= 7) {
          if (!isAnimFinish) {
            isAnimFinish = !isAnimFinish;
            godrays.mesh.dispose();
            ParticleManager.sparkParticleSystem.dispose();
            CameraManager.camera.upperBetaLimit = null;
            CameraManager.camera.lowerBetaLimit = null;

            CameraManager.SetCameraTargetChest();
          }
        }
      }
    });
  }

  setInspector() {
    let scene = this.scene;
    let guiButton = this.guiButton;

    // insert     
    this.scene.onKeyboardObservable.add((kbInfo) => {
      switch (kbInfo.type) {
        case BABYLON.KeyboardEventTypes.KEYDOWN:
          if (kbInfo.event.key === "Insert") {
            if (!this.isInput) {
              console.log("debugLayer.show :::::");
              this.scene.debugLayer.show({
                enablePopup: true,
                overlay: true,
              });
            } else {
              this.scene.debugLayer.hide();
            }
          } else if (kbInfo.event.key === "Escape") {
            Constants.isDetailView = false;

            this.guiText.text = "";
            this.guiButton.getChildByName("but2_button").text = " ";

            guiButton.onPointerUpObservable.clear();
            this.guiButtonX.alpha = 0;
            ContentsManager.UItarget.material.alpha = 0;
            ContentsManager.detailGUIText.text = "";
            scene.beginAnimation(ContentsManager.detailGUI, 10, 0, false);

            CameraManager.camera.target = new BABYLON.Vector3.Zero();
            CameraManager.camera.alpha = -Math.PI / 2;
            CameraManager.camera.beta = 1.2;
            CameraManager.camera.lowerRadiusLimit = 15;
            CameraManager.camera.upperRadiusLimit = 15;
            for (let mesh of scene.meshes) {
              mesh.setEnabled(true);
              ContentsManager.particle1.start();
              ContentsManager.particle2.start();
              ContentsManager.particle3.start();
              ContentsManager.particle3.start();
            }

            var speed = 45;
            var frameCount = 100;

            var animateCameraTargetToPosition = function (cam, speed, frameCount, newPos) {
              var ease = new BABYLON.CubicEase();
              ease.setEasingMode(BABYLON.EasingFunction.EASINGMODE_EASEINOUT);

              var aable1 = BABYLON.Animation.CreateAndStartAnimation("at5", cam, "target", speed, frameCount, cam.target, newPos, 0, ease);
              aable1.disposeOnEnd = true;
            };

            var animateCameraToPosition = function (cam, speed, frameCount, newPos) {
              var ease = new BABYLON.CubicEase();
              ease.setEasingMode(BABYLON.EasingFunction.EASINGMODE_EASEINOUT);
              var aable2 = BABYLON.Animation.CreateAndStartAnimation("at4", cam, "position", speed, frameCount, cam.position, newPos, 0, ease);
              aable2.disposeOnEnd = true;
            };

            animateCameraTargetToPosition(CameraManager.camera, speed, frameCount, new BABYLON.Vector3(0, 0, 0));
            animateCameraToPosition(CameraManager.camera, speed, frameCount, new BABYLON.Vector3(0, 0, -2));
          }
          this.isInput = !this.isInput;
          break;
        default:
          break;
      }
    });
  }
