import React from "react";
import styled from "styled-components";

import Communicator from "../../common/Communicator";
import CommunicatorEvent from "../../common/event/CommunicatorEvent";

let ContainerBox = styled.div`
    pointer-events:none;   
    height:100vh;
    width:100vw;
    list-style-type: none; margin: 0; padding: 0;
`;

let BtnIco = styled.div`
    pointer-events:all;
    cursor: pointer;
    background-image: url(${props => props.ImgURL});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
`;

let BtnWorldBack = styled(BtnIco)`
    position:absolute; left: 26px; top: 20px; float: left; width:92px; height:99px;
    @media screen and (max-width: 500px) {
        left: 16px; top: 15px; width:69px; height:73px;
    }
`;

let BtnCompanyList = styled(BtnIco)`
    position:absolute; right: 26px; top: 20px; float: right; width:92px; height:99px;
    @media screen and (max-width: 500px) {
        right: 16px; top: 15px; width:69px; height:73px;
    }
`;

function MainWorldMenu(props){
    return(
        <ContainerBox>
             {/* <BtnWorldBack ImgURL={IcoWorldBack} onClick={() => {
                Communicator.sendEvent(CommunicatorEvent.CHANGE_CAMERA_MODE, "Play_Mode");
                props.funcControlMode("Default");
            }} />
            <BtnCompanyList ImgURL={IcoCompanyList} onClick={() => {
                props.funcSetCompanyList(true);
            }} /> */}
        </ContainerBox>
    )
}

export default MainWorldMenu;