import React, { useCallback, useEffect, useState } from 'react';
import styled from 'styled-components';

// import useGaTracker from './common/useGaTracker';

import ReactGA from 'react-ga';

import DataManager from '../common/data/DataManager';
import Utils_ui from './common/Utils_ui';
import Constants_ui from './common/Constants_ui';

import PageAuth from './page/PageAuth';
import PageMain from './page/PageMain';
import PageLoading from './page/PageLoading';

let ContainerBox = styled.div`
    margin:0; padding:0;
    width: 100vw; 
    height: ${props => ((props.ContainerHeight))};
    pointer-events:none;
    position: fixed;
`;

let m_arrData = [];

function App() {
    const [strSelectPageName, setSelectPageName] = useState(Constants_ui.PAGE_START);
    let [nVh, setnVh] = useState(1);

    // useGaTracker();

    useEffect(()=>{
        m_arrData = DataManager.GetCompanyDatas();
        let tempData = [...m_arrData];
        // tempData = Utils_ui.SortJSON(tempData, "name", "desc");
        tempData = Utils_ui.SortJSON(tempData, "id", "asc");
        m_arrData = [...tempData];
        // console.log(tempData)

        ReactGA.initialize("UA-172306669-2");
    },[]);

    const setScreenSize = useCallback(() => {
        let tempVh = window.innerHeight;
        if(tempVh === nVh) return;
        setnVh(tempVh);
    }, [nVh])

    window.addEventListener('resize',  () => {
        setScreenSize();
    });

    useEffect(()=>{
        setScreenSize();
    },[setScreenSize]);

    return (
        <ContainerBox ContainerHeight={Utils_ui.PercentToPx(nVh,100)}>
            <SelectSubPage
                strPageName={strSelectPageName}
                funcSetSelectPageName={setSelectPageName}
                nVh={nVh}
            />
        </ContainerBox>
    );
}

function SelectSubPage(props) {
    if (props.strPageName === Constants_ui.PAGE_LOADING){
        return <PageLoading setSelectPageName={props.funcSetSelectPageName} nVh={props.nVh}/>;
    } else if (props.strPageName === Constants_ui.PAGE_AUTH) {
        return <PageAuth setSelectPageName={props.funcSetSelectPageName} nVh={props.nVh}/>;
    } else if (props.strPageName === Constants_ui.PAGE_MAIN) {
        return <PageMain setSelectPageName={props.funcSetSelectPageName} arrCompanyData={m_arrData}  nVh={props.nVh}/>;
    }
}

export default App;