import React,{useEffect, useState} from 'react';
import styled from 'styled-components';
// import ImgBack from "../images/img_sky.png";
// import ImgCloseList from "../images/ico_x.png";
import CompanyListCard from './CompanyListCard'

let ContainerBox = styled.div`
    position: fixed;
    left: 0;
    top:0;
    pointer-events:all;
    height:100vh;
    width:100vw;
    z-index: 1000;
     /* margin:0 auto; padding:0;
     width: 100vw; height: 100vh;
     background-size: cover; */
`;

let BlackBgBox = styled.div`
    position: fixed;
    left: 0;
    top:0;
    pointer-events:all;    
    height:100vh;
    width:100vw;
    background-color: black;
    opacity: 0.9;
    z-index: 1100;
`;

let ContantBgBox = styled.div`
     margin:0 auto; padding:0;
     position:relative;
     top:calc(50vh - 384px);
     box-sizing:border-box;
     width: 768px; height: 768px;
     background-color: white;
     z-index: 1200;
`;

let TopBox = styled.div`
    padding: 20px 0 0;
`;

let TopBoxUl = styled.ul`
    list-style-type:none;
`;

let TopBoxLi = styled.li`
    font-family: 'Spoqa HanSansNeo';
    color: ${props => props.color};
    font-size: ${props => props.fontSize};
    font-weight: ${props => props.fontWeight};
    :nth-child(1) { color: #023c90; font-size: 16px; font-weight: 400; }
    :nth-child(2) { color: #111; font-size: 32px; font-weight: 600; }
    :nth-child(3) { color: #111; }
     @media screen and (max-width: 500px) {
        font-family: 'Spoqa HanSansNeo';
        :nth-child(1) { color: #023c90; font-size: 15px; font-weight: 400; }
        :nth-child(2) { color: #111; font-size: 30px; font-weight: 400; }
        :nth-child(3) { color: #111; font-size: 15px; font-weight: 200;}
    }
`;

let MdBox = styled.div`
    border-top:1px solid #ddd;
    height:38px;
`;


let MdForm = styled.form`   
    margin:0;
    padding:0;
    position:absolute;
`;

let ListContentArea = styled.div`
    pointer-events:all;
    margin:0; padding:0; margin-top:0;
    width: 768px; height: 480px;
    list-style-type: none;
    margin: 0; padding: 0;
    overflow-y:scroll;
    ::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: rgba(136,136,136,1);
    }
    ::-webkit-scrollbar { 
        border-radius: 10px;
        width:5px;
        background-color: rgba(0,0,0,0.3);
    }
    text-align: left;
    box-sizing:border-box;
    color:#000;
    @media screen and (max-width: 500px) {
         width: 410px;
    }
`;

let ListSelect = styled.select`
    font-family: 'Spoqa HanSansNeo';
    margin:-1px 0 0 0px;
    padding: 0 5px; 
    width: 219px; 
    height: 39px; 
    border: 1px solid #ddd; 
    font-size: 15px; 
    cursor: pointer;
    outline:none;
    @media screen and (max-width: 500px) {
         width: 119px;
    }
`;

let ListSelectOption = styled.option`
    @media screen and (max-width: 500px) {
         font-family: 'Spoqa HanSansNeo'; font-size: 30px; color: #fff;  font-weight: 600;
    }
`;

let MdListDiv = styled.div`
    height:80px;
`;

let BtBox = styled.div`
    padding: 20px;
`;

let BtnClose = styled.div`
    position:absolute;
    right: 18px;
    top: 18px;
    width:18px;
    height:18px;
    background-image: url(${props => props.ImgURL});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    cursor: pointer;
    @media screen and (max-width: 768px) {
        top: 18px;  left: 638px; width:18px;  height:18px;
    }
    @media screen and (max-width: 500px) {
        top: 18px;  left: 368px;  width:18px;  height:18px;
    }
    @media screen and (max-width: 410px) and (max-height: 1000px) {
        top: 18px;  left: 338px;  width:18px;  height:18px;
    }
    @media screen and (max-width: 320px) and (max-height: 600px) {
        top: 18px;  left: 368px;  width:18px;  height:18px;
    }
`;

let ImgLogoBox = styled.div`
    left: 40px;
    position:absolute;
    bottom: 30px;
    width:88px;
    height:56px;
    background-image: url(${props => props.ImgURL});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    @media screen and (max-width: 500px) {
        bottom: 50px;
    }
`;


let CompanydataDefault = [
    {
        id: 1,
        name: "",
        homepage: "https://www.netstream.co.kr",
        logo_image: "1_logo.png",
        desc: " ~.",
        loc: "",
    },
    {
        id: 2,
        name: "",
        homepage: "https://www.taglab9.co.kr",
        logo_image: "2_logo.png",
        desc: " . .",
        loc: "",
    },
]


function CompanyListBox(props){
    //console.log("CompanyListBox");

    

    let [Companydata, SetCompanydata] = useState(props.arrCompanyData);
    
    //console.log("Companydata", Companydata);
    let selectList = [" ","", ""];
    let [Selected, setSelected] = useState(" ");

    let handleSelect = (e) => {
        //console.log(" :",e.target.value)
        setSelected(e.target.value);
    };
    
    // useEffect(()=>{
    //     props.funcSendAnalyticsEvent("page","boothList");
    //     console.log("page-boothList");
    // },[])
    
    return(
        <ContainerBox>
            <BlackBgBox/>
                <ContantBgBox>
                    <TopBox>
                        <TopBoxUl>
                            <TopBoxLi>  ()</TopBoxLi>
                            <TopBoxLi>  ()</TopBoxLi>
                            <TopBoxLi>      .</TopBoxLi>
                        </TopBoxUl>
                        <BtnClose onClick={() => {
                            //   
                            //console.log("");
                            props.funcSetCompanyList(false);
                        }} />
                    </TopBox>
                    <MdBox>
                        <MdForm>
                                <ListSelect onChange={handleSelect} value={Selected}>
                                    {selectList.map((data,i) => (
                                        <ListSelectOption value={data} key={i}>
                                            {data}
                                        </ListSelectOption>
                                    ))}
                                </ListSelect>
                        </MdForm>
                    </MdBox>
                <ListContentArea>
                    <MdListDiv>
                        
                        {
                            Companydata.map((data, i) => {
                                return (
                                    <FilterCompanyByLocation 
                                        Selected={Selected}
                                        data={data}
                                        funcSendTenant={props.funcSendTenant}
                                        key={i} />
                                    // <CompanyListCard 
                                    //     data = {data}
                                    //     funcSendTenant={props.funcSendTenant}
                                    //     key={i}
                                    // />
                                )
                            })
                        }
                    </MdListDiv>
                </ListContentArea>
                    <BtBox>
                    </BtBox>
                </ContantBgBox>
        </ContainerBox>
    );
}

function FilterCompanyByLocation(props) {
    //console.log("FilterCompanyLocationData")
    //console.log("Selected :",props.Selected)
    //console.log("data:",props.data)
    //console.log("funcSendTenant:",props.funcSendTenant)
    if (props.Selected ===" ")
    {
        return <CompanyListCard
            data={props.data}
            funcSendTenant={props.funcSendTenant}
            key={props.key}
        />
    } else if (props.Selected === "" && props.data.branch === 1) {
        return <CompanyListCard
            data={props.data}
            funcSendTenant={props.funcSendTenant}
            key={props.key}
        />
    } else if (props.Selected === "" && props.data.branch === 2) {
        return <CompanyListCard
            data={props.data}
            funcSendTenant={props.funcSendTenant}
            key={props.key}
        />
    } else{
        return null;
    }
}

export default CompanyListBox;