import React from 'react';
import styled from 'styled-components';
import ReactGA from 'react-ga';

let CardArea = styled.div`
    :first-child { border-top: 1px solid #ddd; }
    :last-child { border-bottom: 1px solid #ddd; }
    width: 100%; height:80px; display: block;
    border-bottom:1px solid #ddd;
`;

let CardUl = styled.ul`
    float: left;
    list-style-type:none;
    margin:0; padding:0;
    text-align:left;
    :nth-child(1) { width: calc(100% - 100px); padding-left:10px; box-sizing:border-box; }
    :nth-child(2) { width: 80px; margin-top:24px; }
    @media screen and (max-width: 768px) {
        :nth-child(1) { width: calc(100% - 180px); padding-left:10px; box-sizing:border-box; }
        :nth-child(2) { width: 80px; margin-top:24px; }
    }
    @media screen and (max-width: 500px) {
        :nth-child(1) { width: calc(100% - 80px); padding-left:10px; box-sizing:border-box; }
        :nth-child(2) { width: 80px; margin-top:24px; }
    }
    @media screen and (max-width: 410px) and (max-height: 1000px) {
        :nth-child(1) { width: calc(100% - 120px); padding-left:10px; box-sizing:border-box; }
        :nth-child(2) { width: 70px; margin-top:24px; }
    }
    @media screen and (max-width: 320px) and (max-height: 600px) {
        :nth-child(1) { width: calc(100% - 130px); padding-left:10px; box-sizing:border-box; }
        :nth-child(2) { width: 70px; margin-top:24px; }
    }
`;

let TitleLi = styled.li`
    padding:10px; box-sizing: border-box;
    font-family: 'Spoqa HanSansNeo'; height: 40px; font-size: 22px; font-weight: 600;
    color:#000;
    @media screen and (max-width: 500px) {
        font-size: 19px;
        height: 30px;
    }
`;

let DescLi = styled.li`
    padding:10px; box-sizing: border-box;
    font-family: 'Spoqa HanSansNeo'; height: 30px; font-size: 14px; font-weight: 400;
    color:#000;
    @media screen and (max-width: 500px) {
        font-size: 11px;
        height: 30px;
    }
`;

let BtnLi = styled.li`
    pointer-events:all;
    cursor: pointer;
    background-color: #003d90; width:66px; height: 36px; position: relative; text-align: center; 
    line-height: 35px; border-radius: 100px; color: #fff;
`;


function CompanyListCard(props) {
    //console.log("CompanyListCard",props)
    return (
        <CardArea>
            <CardUl>
                {/* <TitleLi>{props.data.name}{(props.data.branch===1)?"()":"()"}</TitleLi> */}
                <TitleLi>{props.data.id}. {props.data.name}</TitleLi>
                <DescLi>{props.data.desc}</DescLi>
            </CardUl>
            <CardUl>
                <BtnLi onClick={()=>{
                    props.funcSendTenant(props.data);
                    ReactGA.event({
                        category: ' _ ',
                        action: ' ',
                        label:props.data.name+" "
                    });
                }}></BtnLi>
            </CardUl>
        </CardArea>
    )
}

export default CompanyListCard;