export default class Utils_ui
{
    'use strict';
    /**
    *===============================================================================
    * SortJSON()        : JSON  
    * @param data       : JSON 
    * @param key        : attribute( )
    * @param type       : "asc","desc"
    *===============================================================================
    */
    static SortJSON (data, key, type) 
    {
        if (type === undefined) type = "asc";

        return (data.sort(function (a, b) {
            let x = a[key];
            let y = b[key];
            let returnData=null;
            if (type === "desc") {
                returnData =  x > y ? -1 : x < y ? 1 : 0;
            } else if (type === "asc") {
                returnData =  x < y ? -1 : x > y ? 1 : 0;
            }

            return returnData;
        }));
    };

    /**
    *===============================================================================
    * PercentToPx()        : full size % 
    * @param nFullSize     :  full 
    * @param nPercent      :   %
    * @param isAddPx       :  "px"    
    *===============================================================================
    */
    static PercentToPx (nFullSize, nPercent, isAddPx=true) 
    {
        let returnData = ""
        
        if(isAddPx) returnData = (nFullSize / 100)*nPercent+"px";
        else returnData = (nFullSize / 100)*nPercent;

        return returnData;
    };

};

